
/* Risorse comuni. */
@import url('./Resources/Fonts/Fonts.scss');


.App {
	position: relative;
	width: 100%;
	height: fit-content;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	
	font-family: 'Mooli Regular';
	
	/* Elementi comuni. */
	
	& label.app-version {
		width: fit-content;
		height: fit-content;
		word-wrap: nowrap;
		
		font-size: 1em;
		
		color: gray;
	}
	
	& img.app-logo {
		content: url('./Resources/AppLogo.png');
	}
	
	/* Interfacce utente. */
	
	& > .user-loading {
		width: 40%;
		min-width: fit-content;
		height: fit-content;
		padding: 8px;
		
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		
		border: 1px solid black;
		border-radius: 3px;
		
		color: black;
		font-size: 12px;
		
		& > h1 {
			font-size: 2em;
		}
		
		& > .app-logo {
			width: 256px;
		}
		
		& > progress {
			width: 90%;
			margin: 2px 0px 2px 0px;
		}
	}

	& > .header {
		position: fixed;
		width: 100%;
		height: 97px;
		top: 0px;
		z-index: 1;

		background-color: lightgray;
		border: 0px solid silver;
		border-bottom-width: 1px;

		& > .app-logo {
			content: url('./Resources/AppLogo.png');
			height: 100%;

			margin-left: 8px;
		}

		& > .app-version {
			position: absolute;
			top: 3px;
			right: 12px;

			color: gray;

			font-family: monospace;
			font-size: 12px;
		}
	}

	& > .workplace {
		width: 100%;
		height: fit-content;
		margin: 96px 0px 128px 0px;
		padding: 8px 0px 0px 0px;

		display: flex;
	}
}

.user
{
	position: relative;
	width: fit-content;
	height: fit-content;
	min-width: 2px;
	min-height: 24px;

	display: flex;
	flex-direction: column;
	gap: 3px;

	&.TextArea
	{
		padding: 3px;

		& > textarea
		{
			resize: none;
			outline: none;
		}
	}
}
