
%message {
	width: 100%;
	height: fit-content;
	margin: 16px 0px 0px 0px;
	
	display: flex;
	flex-direction: column;
	
	font-size: 16px;
	
	& > header {
		width: 100%;
		height: fit-content;
		
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
		
		& > svg, & > img {
			width: 64px;
			height: 64px;
			padding: 8px;
		}
		
		& > h1 {
			font-size: 2em;
		}
	}
}

.document-editor
{
	width: 100%;
	height: 100%;
	
	display: flex;
	flex-direction: column;
	align-items: center;
	
	&.load-validate {
		@extend %message;
		
		color: black;
	}
	
	&.load-fail {
		@extend %message;
		
		color: red;
		
		& > progress {
			width: 80%;
		}
	}
	
	&.no-document {
		@extend %message;
	}
	
	& > .document-form {
		width: 80%;
		height: fit-content;
		
		display: flex;
		flex-direction: column;
		gap: 3px;
		
		font-size: 16px;
		
		& .panel-group {
			width: 100%;
			height: fit-content;
			
			display: flex;
			gap: 3px;
		}
		
		& .panel {
			width: 100%;
			height: fit-content;
			padding: 3px;
			
			display: flex;
			flex-direction: column;
			align-items: center;
			
			background-color: lightgray;
			border: 1px solid silver;
			border-radius: 3px;
			
			& > .header {
				position: relative;
				width: 100%;
				height: 24px;
				padding: 0px 8px 0px 8px;
				
				display: flex;
				flex-direction: row;
				align-items: center;
				
				& > .icon {
					width: 24px;
					height: 24px;
					margin: 0px 10px 0px 0px;
				}
				
				& > .title {
					font-size: 1.2em;
				}
				
				& > .visibility-button {
					position: absolute;
					width: calc(24px + 16px);
					height: 24px;
					right: 0px;
					padding: 2px 8px 2px 8px;
					
					border: none;
					border-radius: 3px;
					background-color: transparent;
					
					& > svg, & > img {
						width: 100%;
						height: 100%;
					}
					
					&:hover {
						background-color: rgba(0, 0, 0, 0.05);
					}
					
					&:active {
						background-color: rgba(0, 0, 0, 0.2);
					}
				}
			}
			
			& > .line {
				width: 60%;
				height: 1px;
				
				border: 0px dotted silver;
				border-top-width: 1px;
			}
			
			& > .content {
				width: 100%;
				height: fit-content;
				margin: 3px 0px 0px 0px;
				
				& > * {
					width: 100%;
				}
			}
		}
		
		& > .export-panel {
			width: 100%;
			height: fit-content;
			margin: 48px 0px 0px 0px;
			
			display: flex;
			flex-direction: column;
			justify-content: flex-end;
			align-items: flex-end;
			
			font-size: 18px;
			
			& > .filename {
				width: 40%;
				min-width: 200px;
				margin: 0px 0px 8px 0px;
			}
			
			& > .file-formats {
				width: fit-content;
				height: 32px;
				
				display: flex;
				flex-direction: row;
				gap: 3px;
				
				& > button {
					width: 200px;
					height: 100%;
					padding: 2px;
					
					display: flex;
					flex-direction: row;
					align-items: center;
					
					& > svg, & > img {
						width: 24px;
						height: 100%;
						margin: 0px 6px 0px 0px;
					}
					
					&.docx {
						color: blue;
					}
					
					&.pdf {
						color: red;
					}
				}
			}
		}
	}
}
