
.Textarea
{
	width: fit-content;
	height: fit-content;
	
	display: flex;
	flex-direction: column;
	
	& > textarea {
		width: 100%;
		height: 100%;
		padding: 3px;
		
		resize: none;
		
		background-color: white;
		border: 1px solid silver;
		border-radius: 3px;
		border-bottom-left-radius: 0px;
		border-bottom-right-radius: 0px;
		
		&:hover, &:focus {
			border-color: black;
		}
	}
	
	& > footer {
		width: 100%;
		height: 24px;
		
		display: flex;
		flex-direction: row;
		
		background-color: white;
		border: 1px solid silver;
		border-radius: 3px;
		border-top-width: 0px;
		border-top-left-radius: 0px;
		border-top-right-radius: 0px;
		
		& > button {
			width: 32px;
			height: 100%;
			
			background-color: transparent;
			border: none;
			color: black;
			
			& > svg, img {
				width: 100%;
				height: 100%;
			}
			
			&:hover {
				background-color: lightgray;
			}
			
			&:active {
				background-color: silver;
			}
			
			&:disabled {
				color: silver;
				
				&:active {
					background-color: lightgray;
				}
			}
		}
	}
}
